import { IPageRoute } from 'library/common/navigation';

export const allProductsRoute: IPageRoute = {
  title: 'All products',
  path: '/products/allproducts',
  parent: () => productTableRoute,
  component: () => import('./product-table-page')
}

export const attributesProductRoute: IPageRoute = {
  title: 'Attributes',
  path: '/products/attributes',
  parent: () => productTableRoute,
  component: () => import('./product-table-page')
}

export const productDetailRoute: IPageRoute = {
  title: 'Product edit',
  path: '/products/:id',
  parent: () => productTableRoute,
  component: () => import('./product-page')
}

export const productNewRoute: IPageRoute = {
  ...productDetailRoute,
  path: '/products/new',
  title: 'New product'
}

const productTableRoute: IPageRoute = {
  title: 'Products',
  path: '/products',
  icon: 'fa fa-cubes',
  redirect: () => allProductsRoute,
  component: () => import('./product-table-page'),
  children: [
    allProductsRoute,
    attributesProductRoute
  ]
}

export const productRoutes: IPageRoute[] = [
  productTableRoute,
  allProductsRoute,
  attributesProductRoute,
  productDetailRoute,
  productNewRoute,
]

export const productsMenus: IPageRoute[] = [productTableRoute];