import { reducerWithInitialState } from 'typescript-fsa-reducers';
import { FetchDataState } from './types';
import { setFetchingAction } from './actions';

export const fetchDataReducer = reducerWithInitialState<FetchDataState>({ })
  .case(setFetchingAction, (state, { fetching }) => ({
    ...state,
    fetching,
  }))