import React from 'react';
import { useField } from 'formik';
import { FieldWrapper } from './wrapper';
import { FieldProps } from './types';
import { SwitchBox } from 'library/components';

export const SwitchField = ({ name, label }: Pick<FieldProps<boolean>, 'name' | 'label'>) => {
  const [field, meta, helpers] = useField<boolean>({ name });  
  const handleChange = (value: boolean) => helpers.setValue(value);
  return (
    <FieldWrapper meta={meta}>
      <SwitchBox label={label} checked={field.value!} onChange={handleChange} />
    </FieldWrapper>
  )
}