import React from 'react';
import clx from 'classnames';
import styled from 'styled-components';
import { PaginationProps, PageButtonProps} from './types';

export const Pagination = ({
  showing,
  totalCount,
  pageCount, displayIndex, onChange, onFocus, onBlur, next, prev, goNext, goPrev,
}: PaginationProps) => {
  const inputRef = React.useRef(null);

  const Span = ({ children }: any) => <span className="m-r-xs">{children}</span>;

  return (
    <PaginationContainer>
      <h3 className="m-r-sm">
        <Span>showing</Span>
        <Span>{showing}</Span>
        <Span>of</Span>
        <Span>{totalCount}</Span>
        <Span>{totalCount > 1 ? 'records' : 'record'}</Span>
      </h3>
      <InputFormGroup className="form-group m-r-sm m-t-xs" style={{ marginTop: 10, minWidth: 60 }}>
        <input
          ref={inputRef as any}
          className="form-control"
          value={displayIndex}
          onChange={onChange}
          onBlur={onBlur}
          onFocus={onFocus}
          disabled={!totalCount}
          onKeyDown={(e) => {
            if (e.keyCode === 13) {
              onChange(e);
              onBlur(e, true);
            }
          }}
        />
      </InputFormGroup>
      <h3 className="m-r-sm">
        <Span>of</Span>
        <Span>{pageCount}</Span>
        <Span>{pageCount > 1 ? 'pages' : 'page'}</Span>
      </h3>
      <PageButton icon="fa fa-chevron-left" className="m-r-sm" enabled={prev} click={goPrev} />
      <PageButton icon="fa fa-chevron-right" enabled={next} click={goNext} />
    </PaginationContainer>
  );
};

const PageButton = ({
  icon, click, enabled, className,
}: PageButtonProps) => {
  const handleClick = () => {
    if (enabled) {
      click();
    }
  };

  return (
    <button
      className={clx('btn', enabled && 'btn-info', !enabled && 'btn-default', className)}
      onClick={handleClick}
      type="button"
    >
      <i className={icon} />
    </button>
  );
};

export const PaginationContainer = styled.div`
  display: flex;
  align-items: center;
  h3 {
    font-weight: 300;
  }
`;

export const InputFormGroup = styled.div`
  width: 3rem;
`;
