/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect } from 'react';
import {BaseNode, UseHierarchyProps} from './types';
import { sameArrays } from './utils';

export function useHierarchy<T extends BaseNode>({ 
    data: initialData, 
    expandedIds: expanded,
    onExpand,
  }: UseHierarchyProps<T>) {

  const [data, setData] = useState<T[]>(initialData);
  const [expandedIds, setExpandedIds] = useState<string[]>(expanded ?? []);
  const [nodes, setNodes] = useState<T[]>([]);

  const [rotatingId, setRotatingId] = useState<any>(null);
  const [collapsingId, setCollapsingId] = useState<any>(null);

  const clearAnimation = () => {
    setRotatingId(undefined);
    setCollapsingId(undefined);
  }

  const isEmpty = (val: any) => val === undefined || val === null;
  
  const filterParent = (parent_id?: string) => (d: T) => isEmpty(parent_id) ? isEmpty(d.parent_id)
    : d.parent_id === parent_id;

  const buildNodes = (level: number, parent_id?: string) => data
    .filter(filterParent(parent_id))
    .reduce((acc, node) => {
      const expanded = expandedIds.some(i => i === node.id);
      let _nodes = [...acc, { ...node, level, expanded }];
      if (expanded) {
        _nodes = [..._nodes, ...buildNodes(level + 1, node.id)];
      }
      return _nodes;
    }, [] as T[]);

  const handleToggleNode = (node: T) => {
    const expanded = expandedIds.some(i => i === node.id);
    if (expanded) {
      setCollapsingId(node.id);
      setRotatingId(undefined);
      setExpandedIds(prev => prev.filter(i => i !== node.id));
    } else {
      setRotatingId(node.id);
      setCollapsingId(undefined)
      setExpandedIds(prev => [...prev, node.id]);
    }
    if (onExpand) {
      onExpand(expanded, node);
    }
  };

  const hasChildren = (node: T) => data.some(n => n.parent_id === node.id);

  useEffect(() => {
    setNodes(() => buildNodes(0, undefined));
  }, [expandedIds, data])

  useEffect(() => {
    setData(initialData);
  }, [initialData]);

  useEffect(() => {
    if (expanded?.length && !sameArrays(expanded, expandedIds)) {
      setExpandedIds(expanded);
    }
  }, [expanded]);

  useEffect(() => {
    if (rotatingId || collapsingId) {
      setTimeout(() => {
        clearAnimation();
      }, [500])
    }
  }, [rotatingId, collapsingId])

  return {
    nodes,
    rotatingId,
    collapsingId,
    hasChildren,
    toggleNode: handleToggleNode,
  }
}