import React from 'react';
import styled from 'styled-components';
import { FieldWrappperProps } from './types';

export function FieldWrapper<T>({ label, children }: FieldWrappperProps<T>) {
  return (
    <Root className="form-group">
      {label && <h3 className="field-label">{label}</h3>}
      <div className="field-content">{children}</div>
    </Root>
  )
}

const Root = styled.div`
  display: grid;
  grid-template-areas: 
    "label"
    "content ";
  .field-label {
    grid-area: label;
  }
  .field-content {
    grid-area: content;
  }
`;