/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import classnames from 'classnames';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import {useTopbar} from './use-topbar';
import {PageTitle} from './page-title';

type TopbarProps = {
  pageTitle?: string | React.ReactNode | JSX.Element;
}

export const Topbar = ({ pageTitle: documentTitle }: TopbarProps) => {
  const {crumbRoutes, pageTitle} = useTopbar();
  return (
    <Row className="row border-bottom">
      <nav className="navbar navbar-static-top white-bg" role="navigation" style={{ marginBottom: 0, paddingBottom: 7 }}>
        <NavHeader className="navbar-header">
          <div className="col-lg-9">
            {pageTitle && (<PageTitle title={documentTitle ?? pageTitle} />)}
            <OlCrumbs className="breadcrumb">
              {crumbRoutes.map((crmb, i) => {
                const isLast = i === crumbRoutes.length - 1;
                return (
                  <li key={i} className={classnames("breadcrumb-item", isLast && 'active')}>
                    {!isLast ? <Link className='text-success' to={crmb.path}>{crmb.title.toLowerCase()}</Link> : <span>{crmb.title.toLowerCase()}</span>}
                  </li>
                )
              })}
            </OlCrumbs>
          </div>
        </NavHeader>
        <ul className="nav navbar-top-links navbar-right">
          <li>
            <a href="#">
              <i className="fa fa-sign-out" /> Log out
            </a>
          </li>
        </ul>
      </nav>
    </Row>
  )
}


const Row = styled.div`
  padding-right: 20px;
`;

const NavHeader = styled.div`
  width: 80%;
  padding-left: 36px;
`;

const OlCrumbs = styled.ol`
 font-size: 16px;
`;