import React from 'react';
import classnames from 'classnames';

type ModalBodyProps = {
  className?: string;
  children: React.ReactNode | React.ReactNode[];
}
export const ModalBody = ({ className, children }: ModalBodyProps) => (
  <div className={classnames('modal-body', className)}>
    {children}
  </div>
)

