/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect } from 'react';
import {PageData, UseDataTableProps} from './types';
import {LIMIT} from './constants';

export function useDataTable<T>({ onFetchStart, onFetchCompleted }: UseDataTableProps<T>) {
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const [fetching, setFetching] = useState(true);

  const [data, setData] = useState<PageData<T>>({
    count: 0,
    items: [],
  });

  const _onFetchEnd = (data: PageData<T>) => {
    setData(onFetchCompleted ? onFetchCompleted(data) : data);
    setFetching(false);
  };
  const _fetchData = async () => {
    setFetching(true);
    const res = await onFetchStart({ page: currentIndex, limit: LIMIT });
    _onFetchEnd(res);
  }

  useEffect(() => {
    _fetchData();
  }, [currentIndex]);

  return {
    currentIndex,
    fetching,
    data,
    setCurrentIndex,
  }
}