import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {Table} from 'library/components/table';
import { TableData } from 'library/components/table';
import {DataTableProps} from './types';
import {useDataTable} from './use-data-table';
import {usePagination, Pagination} from '../Pagination';
import { LIMIT } from './constants';

export function DataTable<T extends TableData>({ onFetchStart, onFetchCompleted, addItemUrl, addItemTitle, ...rest }: DataTableProps<T>) {

  const {currentIndex: index, data: { count, items }, setCurrentIndex: setIndex, } = useDataTable({onFetchStart, onFetchCompleted,});
  const paging = usePagination({
    pageIndex: index,
    pageSize: LIMIT,
    totalCount: count,
    onPageIndexChange: setIndex
  })

  return (
    <div>
      <TableControls>
        <LeftControls>
          {addItemUrl && (
            <Link to={addItemUrl} className="btn btn-w-m btn-primary btn-outline mr-2">{addItemTitle}</Link>
          )}
        </LeftControls>
        <Pagination
          {...paging}
          goNext={() => setIndex(index + 1)}
          goPrev={() => setIndex(index - 1)}
          pageCount={paging.totalPageCount}
          totalCount={paging.totalCount}
        />
      </TableControls>
      {/* <Hr /> */}
      <Table<T> {...rest} data={items} />
    </div>
  )
}

const TableControls = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  .text-primary {
     color: #007bff !important;
  }
`;

const LeftControls = styled.div`
  display: flex;
  align-items: center;
`;