/* eslint-disable no-loop-func */
import { BaseNode } from "../hierarchy/types";
import { UseSelectableHierarchyProps } from './types';

export const useSelectableHierarchy = <T extends BaseNode>({
  data, selection, onCheck, onUncheck,
}: UseSelectableHierarchyProps<T>) => {

  const toggleSelection = (node: T) => (checked: boolean) => {
    if (checked) {
      onCheck(node);
    } else {
      onUncheck(node);
    }
  }

  const isRoot = (node: T) => node.parent_id === undefined || node.parent_id === null;
  const isChecked = (node: T) => !!selection.find(n => n.id === node.id);
  const hasChildren = (node: T) => data.some(d => d.parent_id === node.id);

  const getIntent = (node: T) => {
    const _isRoot = isRoot(node);
    const _hasChildren = hasChildren(node);

    if (!_isRoot && !_hasChildren) { // default leaf: ml-5
      return 'ml-4'
    }

    if (isRoot && !_hasChildren) { // root-leaf
      return 'ml-4'
    }

    return 'ml-2';
  }

  const isIntermediary = (node: T) => {
    const children = data.filter(d => d.parent_id === node.id);
    const selected = children.filter(c => isChecked(c)).length;
    return selected > 0 && selected !== children.length;
  };

  return {
    isChecked,
    getIntent,
    isIntermediary,
    toggleSelection,
  }

}