import React from 'react';
import {Hierarchy} from 'library/components';
import {SelectableHierarchyProps} from './types';
import { BaseNode } from '../hierarchy/types';
import {getCheckableLabel} from './checkable-label';
import {useSelectableHierarchy} from './use-checkable-hierarchy';

export function CheckableHierarchy<T extends BaseNode>({ onLabel, ...rest  }: SelectableHierarchyProps<T>) {
  const { data, onCheck, onUncheck, selection } = rest;
  const { toggleSelection, isChecked, getIntent } = useSelectableHierarchy({ data, onCheck, onUncheck, selection })

  return <Hierarchy {...rest} onLabel={getCheckableLabel<T>({ onLabel, getIntent, toggleSelection, isChecked })} />
}
