/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import { Table } from 'library/components/table';
import { BaseNode, HierarchyProps } from './types';
import { useHierarchy } from './useHierarchy';
import { DefaultRow } from './default-handles';

export function Hierarchy<T extends BaseNode>({ data, expandedIds, onLabel }: HierarchyProps<T>) {

  const { nodes, toggleNode, hasChildren, rotatingId, collapsingId } = useHierarchy({ data, expandedIds });

  const renderLabel = (row: T) => {
    const Label = onLabel({ row });
    if (typeof Label === "function") {
      return <Label />
    }
    return Label
  }

  return (
    <Table<T>
      className="hierarchy-table"
      headers={[""]}
      data={nodes}
      row={({ row: node }) => {
        return (
          <DefaultRow<T>
            row={node}
            rotatingId={rotatingId}
            collapsingId={collapsingId}
            displayArrow={hasChildren(node)}
            onToggle={toggleNode}
          >
            {renderLabel(node)}
          </DefaultRow>
        )
      }}
    />
  )
}
