import React, { useState } from 'react';
import styled from 'styled-components';
import {useField} from 'formik';
import {FieldWrapper} from './wrapper';
import { FieldProps} from './types';

export function TextField({ name, label, placeholder }: Pick<FieldProps<string>, 'name'|'label'|'placeholder'>) {
  const [field, meta] = useField<string>({ name });
  return (
    <FieldWrapper label={label} meta={meta}>
      <input type="text" {...field} placeholder={placeholder} className="form-control p-md" />
    </FieldWrapper>
  )
}
// pattern="^(?=.?\d)\d{0,14}(\.?\d{0,6})?$"
export function CurrencyField({ name, label }: Pick<FieldProps<string>, 'name' | 'label'>) {
  const [field, meta] = useField<number>({ name });
  const [focused, setFocused] = useState<boolean>(false);
  
  return (
    <FieldWrapper label={label} meta={meta}>
      <CurrencyContent>
        <div className="input-icon input-icon-right">
          {!focused && <input
            {...field}
            pattern="[0-9]+([,\.][0-9]+)?"
            value={Intl.NumberFormat('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(field.value)}
            onFocus={() => setFocused(true)}
            className="form-control p-md"
            placeholder="0.00"
          />}
          {focused && <input
            type="number"
            {...field}
            onBlur={() => setFocused(false)}
            className="form-control p-md"
            placeholder="0.00"
          /> 
          }
          <i className="fa fa-eur" />
        </div>
      </CurrencyContent>
    </FieldWrapper>
  )
}

const CurrencyContent = styled.div`
  position: relative;

  /* Chrome, Safari, Edge, Opera */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type=number] {
    -moz-appearance: textfield;
  }

  .input-icon > i {
    position: absolute;
    display: block;
    transform: translate(0, -50%);
    top: 50%;
    pointer-events: none;
    width: 25px;
    text-align: center;
	  font-style: normal;
  }

  .input-icon > input {
    padding-left: 25px;
	  padding-right: 0;
  }

  .input-icon-right > i {
    right: 0;
  }

  .input-icon-right > input {
    padding-left: 0;
    padding-right: 25px;
    text-align: right;
  }
`;