import React from 'react';
import { Page, DataTable, Panel, Table } from 'library/components';
import { useFetchData } from 'library/common';
import { getRouteUrl } from 'library/common/navigation/utils';
import { config } from 'config';
import { Order } from './types';
import { orderEditRoute } from './routes';
import moment from 'moment';

export const Orders = () => {
  const { fetchOrders } = useOrdersTablePage();
  return (
    <Page>
      <Panel>
        <DataTable<Order>
          onFetchStart={fetchOrders()}
          headers={['Order', 'Date', 'Customer', 'Payment', 'Status', <Table.NumericHeader>Total</Table.NumericHeader>]}
          row={({ row }) => (
            <Table.Row>
              <Table.LinkColumn path={getRouteUrl(orderEditRoute.path, { id: row.id })} params={row}>
                {row.number}
              </Table.LinkColumn>
              <Table.TextColumn>{moment(row.date_created).format("MMM D")}</Table.TextColumn>
              <Table.LinkColumn path={getRouteUrl(orderEditRoute.path, { id: row.id })} params={row}>
                {row.billing.name}
              </Table.LinkColumn>
              <Table.Column>
                <PaymentColumn row={row} />
              </Table.Column>
              <Table.TextColumn>{row.status}</Table.TextColumn>
              <Table.CurrencyColumn value={row.grand_total} />
            </Table.Row>
          )}
        />
      </Panel>
    </Page>
  )
}

type PaymentColumnProps = {
  row: Order;
}
const PaymentColumn = ({ row: { paid, billing: { card } } }: PaymentColumnProps) => {
  const CardIcon = () => card ? <i className="fa fa-cc-visa mr-2" /> : <></>;
  return (
    <>
      <CardIcon />
      <span>{paid ? 'Paid' : 'Unpaid'}</span>
    </>
  )
}

const useOrdersTablePage = () => {
  const { fetchData } = useFetchData();
  const fetchOrders = () => async ({ page, limit }: any) => {
    const res = await fetchData<{ count: number, results: Order[] }>({
      url: `${config.apiSwell}/orders`,
      method: 'POST',
      body: {
        paging: { page, limit }
      }
    });
    const { count, results: items } = res.data;
    return {
      count,
      items: items as Order[]
    }
  }

  return {
    fetchOrders,
  }
}

export default Orders;
