import React from 'react';
import {Page} from 'library/components/page';
import {DataPageProvider} from './data-page-provider';
import { DataPageProps, BaseItem} from './types';

export function DataPage<T extends BaseItem>({ children, ...rest }: DataPageProps<T>){
  const { className, pageTitle } = rest;
  return (
    <Page className={className} pageTitle={pageTitle}>
      <DataPageProvider {...rest}>
        {children}
      </DataPageProvider>
    </Page>
  )
}