import React from 'react';
import styled from 'styled-components';
import AliceCarousel from 'react-alice-carousel';
import { CarouselProps } from './types';

export const Carousel = ({ items, ...rest }: CarouselProps) => {
  return (
    <AliceCarousel mouseTrackingEnabled
      infinite={false}
      responsive={{
        0: { items: 1 },
        480: { items: 2 },
        768: { items: 4 },
        1024: { items: 5 },
      }}
      dotsDisabled
      items={items.map((src, i) => (
        <ImageItem key={i} index={i} src={src} />
      ))}
    />
  )
};
// m.file.url
type ImageItemProps = {
  index: number;
  src: string;
}
const ImageItem = ({ index, src }: ImageItemProps) => {
  return (
    <ImageContainer>
      <span className="badge index">{`${index + 1}`}</span>
      <i className="fa fa-trash icon-delete" onClick={() => alert('under construction')} />
      <div className="content">
        <div>
          <img style={{
            maxWidth: 150,
            maxHeight: 150,
          }} className="img-thumbnail" src={src} alt="item" />
        </div>
      </div>
    </ImageContainer>
  )
}

const ImageContainer = styled.div`
  position: relative;
  cursor: pointer;
  .index {
    top: 4px;
    left: 4px;
    position: absolute;
  }
  .icon-delete {
    transition: all 0.3s;
    position: absolute;
    left: 120px;
    top: 5px;
    margin-right: 10px;
    opacity: 0.1;
    font-size: 16px;
  }
  &:hover {
    .icon-delete {
      opacity: 1;
    }
  }
  .content {
    display: flex;
    /* justify-content: center; */
  }
`;