/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { isEqual } from 'lodash';
import toastr from 'toastr';
import { config } from 'config';
import { useFetchData, useSafeState } from 'library/common';
import { UseDataPageProps, BaseItem} from './types';

export function useInternalDataPage<T extends BaseItem>({ model, initialValues, onFetched }: UseDataPageProps<T>) {
  const [item, setItem] = useSafeState<T>(initialValues!);
  const [fetching, setFetching] = useSafeState(true);
  const [saving, setSaving] = useSafeState(false);
  const { id } = useParams<T>();
  const { fetchData } = useFetchData();
  const editMode = !!id;

  const fetchItem = async () => {
    const res = await fetchData<T>({
      url: `${config.apiSwell}/${model}/${id}`,
    });
    const { data } = res;
    const _data = onFetched ? await onFetched(data) : data;
    setItem(_data);
    setFetching(false);
    return res;
  }

  const updateItem = async (values: T) => {
    setSaving(true);

    let _values = Object.keys(values).filter(k => !Array.isArray(values[k])).reduce((acc, k) => ({
      ...acc,
      [k]: values[k]
    }), {});

    let arrayKeys = Object.keys(values).filter(k => Array.isArray(values[k]));
    let arrayValues = arrayKeys.length ? arrayKeys.filter(k => !isEqual(item[k], values[k])).reduce((acc, k) => ({
      ...acc,
      k: values[k] ?? [],
    }), {}) : undefined;

    const updatedValues = {
      ..._values,
      ...(arrayValues && Object.keys(arrayValues).length ? {
        $set: { ...arrayValues }
      } : {}),
    }

    let error = undefined;

    return await fetchData<T>({
      url: `${config.apiSwell}/${model}/${id}`,
      method: 'PUT',
      body: {
        ...updatedValues,
      }
    })
      .then(async (res) => {
        if (!res.error) {
          setItem(updatedValues as any);
          toastr.success('Data saved successfully', 'Success');
        } else {
          error = res.error;
          toastr.error(typeof res.error === 'object' ? JSON.stringify(res.error) : res.error, 'Error')
        }
        return res;
      }).catch(err => {
        error = err;
        toastr.error(JSON.stringify(err), 'Error');
      })
      .finally(() => {
        setSaving(false);
        if (!error) {
          // window.location.reload();
        }
      });
  }

  useEffect(() => {
    if (editMode) {
      fetchItem();
    }
  }, []);

  useEffect(() => {
    if (initialValues) {
      setItem(initialValues);
    }
  }, [initialValues])

  return {
    item: item as T,
    saving,
    fetching,
    updateItem,
  }
}