import React from 'react';
import styled from 'styled-components';
import {Formik, useFormikContext} from 'formik';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';

import {Button, useDataPage} from 'library/components';
import {DataFormProps} from './types';
import { BaseItem } from '../data-page/types';

const swal = withReactContent(Swal)

export function DataForm<T extends BaseItem>({ title, initialValues, onSubmit, children }: DataFormProps<T>){
  return (
    <Formik<T>
      initialValues={initialValues}
      enableReinitialize={true}
      onSubmit={(values) => onSubmit(values as any)}
    >
      {({
        dirty,

      }) => (
        <>
            <DataFormControls dirty={dirty} title={title} />
            {children}
        </>
      )}
    </Formik>
  )
}

type DataFormControlsProps = {
  dirty: boolean;
  title: string | React.ReactNode | JSX.Element;
}
const DataFormControls = ({ title: Title, dirty }: DataFormControlsProps) => {
  const { submitForm, resetForm } = useFormikContext();
  const { saving } = useDataPage();

  const handleReset = () => {
    if (dirty) {
      swal.fire({
        icon: 'warning',
        title: "Warning",
        text: "Changes made will be lost. Continue?",
        showCancelButton: true,
        cancelButtonColor: '#d33',
      }).then((res) => {
        if (res.isConfirmed) {
          resetForm();
        }
      });
    }
  }
  const DataTitle = typeof Title === 'function' ? () => <Title /> : () => <>{Title}</>;

  return (
    <Container>
      <h2 className="text-success">
        <DataTitle />
      </h2>
      <ButtonContainer>
        <div>
          <FormButton disable={!dirty} color="success" className="mr-3" onClick={submitForm}>
            {saving ? <>Save <i className="fa fa-spin fa-spinner ml-2" /></> : <>Save</>}
          </FormButton>
        </div>
        <div>
          <FormButton disable={!dirty} color="warning" onClick={handleReset} >Reset</FormButton>
        </div>
      </ButtonContainer>
    </Container>
  )
}

const Container = styled.div`
  display: flex;
  justify-content: space-between;
`;
const ButtonContainer = styled.div`
  display: flex;
`;

const FormButton = styled(Button)`
  /* width: 80px; */
  transition: all .3s ease;
  display: flex;
  align-items: center;
`;