import React from 'react';

import { DataForm } from 'library/components/data-form';
import { DataFormPageProps, UseDataFormPageProps } from './types';
import { BaseItem, useDataPage, Spinner } from 'library/components';

export function DataFormPage<T extends BaseItem>({
  title, children, ...rest
 }: DataFormPageProps<T>) {
  const { item, fetching, updateItem } = useDataFormPage<T>({...rest});
  
  const Title = fetching ? () => (
    <>
      <span>{title}</span> <Spinner />
    </>
  ) : () => <span>{title}</span>;

  return (
    <DataForm<T>
      title={Title}
      initialValues={item}
      onSubmit={async (values) => await updateItem(values).then(res => res.success) }
    >
      {children}
    </DataForm>
  )
}

function useDataFormPage<T extends BaseItem>({ onSubmit  }: UseDataFormPageProps<T>) {
  const { item, fetching, updateItem } = useDataPage<T>();

  function handleSubmit(values: T) {
    return onSubmit?.(values) ?? updateItem(values);
  }

  return {
    item,
    fetching,
    updateItem: handleSubmit,
  }
}
