import React from 'react';
import classnames from 'classnames';

type ButtonProps = {
  color?: 'default'|'primary'|'success'|'info'|'warning'|'danger'|'link'|'white';
  size?: 'xs'|'sm'|'lg';
  link?: boolean;
  outline?: boolean;
  disable?: boolean;
  block?: boolean;
  onClick?: () => void;
  className?: string;
  children: React.ReactNode;
}
export const Button = ({ onClick, color = "primary", size, outline, disable, 
  className, block, link, children}: ButtonProps) => {
  const handleClick = (e: any) => {
    if (onClick && !disable) {
      onClick();
    }
  }
  return (
    <button type="button" className={classnames("btn", 
      !disable && !link && `btn-${color}`,
      block && 'btn-block',  
      size && `btn-${size}`, 
      (!disable && outline) && `btn-outline`, 
      link && 'btn-link',
      disable && 'btn-default', 
      className )} 
      onClick={handleClick}
    >
      {children}
    </button>
  )
}