import React from 'react';
import classnames from 'classnames';
import styled from 'styled-components';
import { TableProps, TableHeader, TableHeaderObj, TableData} from './types';
import {TableRow} from './table-row';
import {Column, TextColumn, LinkColumn, ImageColumn, CurrencyColumn, NumericHeader} from './table-columns';

export function Table<T extends TableData>({ headers, className, row, data }: TableProps<T>) {
  return (
    <TableView className={classnames("table table-hover table-borderless", className )}>
      <TableHeaders headers={headers} />
      <TableRows data={data} row={row} />
    </TableView>
  )
};

type TableHeadersProps = {
  headers: TableHeader[];
}
const Th = ({children, ...rest}: any) => <th {...rest}>{children}</th>
const TableHeaders = ({ headers }: TableHeadersProps) => (
  <thead className="thead-dark">
    <tr>
      {headers.map((Head, i) => {
        if (typeof Head === "function") {
          return <Th key={i}><Head /></Th>
        }
        if ((Head as TableHeaderObj).title) {
          const { title, width } = (Head as TableHeaderObj);
          return <Th key={i} style={width ? { width }: {}} >{title}</Th>
        }
        return <Th key={i}>{Head}</Th>
      })}
    </tr>
  </thead>
);

function TableRows<T extends TableData>({ data, row: Row }: Omit<TableProps<T>, 'children' | 'headers'>) {
  return (
    <tbody>
      {data.map((r, i) => (
        <Row key={r.id} row={r} index={i} />
      ))}
    </tbody>
  )
}

Table.Row = TableRow;
Table.Column = Column;
Table.TextColumn = TextColumn;
Table.LinkColumn = LinkColumn;
Table.ImageColumn = ImageColumn;
Table.CurrencyColumn = CurrencyColumn;
Table.NumericHeader = NumericHeader;

const TableView = styled.table`
  &.hierarchy-table tbody tr:nth-of-type(odd) {
    background-color: #F0F5FB;
    &:hover {
      color: #212529;
      background-color: rgba(0, 0, 0, 0.075);
    }
  }
`;