import React from 'react';
import styled from 'styled-components';
import {useAppStoreSelector} from 'library/common/store';
import { Spinner } from 'library/components/spinner';

type PageTitleProps = {
  title: string | React.ReactNode | JSX.Element;
}

export const PageTitle = ({ title: Title }: PageTitleProps) => {
  const buzy = useAppStoreSelector(state => state.fetchdata.fetching);
  
  return (
    <H2 className="text-info" >
      { typeof Title === 'function' ? <Title /> : <span>{Title}</span>}
      {buzy && <Spinner />}
    </H2>
  )
}

const H2 = styled.h2`
  display: flex;
  align-items: center;
`;