import React from 'react';
import styled from 'styled-components';
import classnames from 'classnames';
import {Navigation} from 'library/common/navigation';
import {Progress} from './progress';
import {Topbar} from './topbar';
import {PageProps} from './types';
import {PageContext} from './page-context';

const wrapClass = 'gray-bg d-flex flex-column';
const contentClass = 'wrapper wrapper-content d-flex flex-column full-flex flex-1 animated fadeInRight';

export const Page = ({ className, pageTitle, children }: PageProps) => (
  <PageContext>
    <Progress />
    <Navigation />
    <div id="page-wrapper" className={wrapClass} style={{ padding: 0 }}>
      <Topbar pageTitle={pageTitle} />
      <PageWrapperContent className={classnames(contentClass, className)}>
        {children}
      </PageWrapperContent>
    </div>
  </PageContext>
);

const PageWrapperContent = styled.div`
  flex: 1;
  margin-bottom: 10px;
`;