import { IPageRoute } from 'library/common/navigation';

const allOrdersRoute: IPageRoute = {
  title: 'All orders',
  path: '/orders/allorders',
  parent: () => ordersRoute,
  component: () => import('./orders-table-page')
}

export const orderEditRoute: IPageRoute = {
  title: 'Order edit',
  path: '/orders/:id',
  parent: () => ordersRoute,
  component: () => import('./order-page')
}

const ordersRoute: IPageRoute = {
  title: 'Orders',
  path: '/orders',
  icon: 'fa fa-bar-chart',
  redirect: () => allOrdersRoute,
  component: () => import('./orders-table-page'),
  children: [
    allOrdersRoute,
  ]
}

export const ordersModuleRoutes: IPageRoute[] = [
  ordersRoute,
  allOrdersRoute,
  orderEditRoute
]

export const ordersModuleMenus: IPageRoute[] = [ordersRoute];