/* eslint-disable react-hooks/exhaustive-deps */
import { TabsProps, TabProps} from './types';
import React, { useState, createContext, useContext, useEffect } from 'react';

const useTabsContext = ({ activeTab, onTabChange }: Omit<TabsProps, 'children'>) => {
  const [active, setActive] = useState(activeTab);
  const [tabs, setTabs] = useState<TabProps[]>([]);
  const initTab = (tab: TabProps) => setTabs(prev => [...prev, tab]);

  const handleTabChange = (tab: string) => {
    if (onTabChange) {
      onTabChange(tab);
    } else {
      setActive(tabs.find(t => t.name === tab)?.name)
    }
  }

  useEffect(() => {
    if (activeTab && active !== activeTab) {
      setActive(activeTab)
    } else {
      if (!active && tabs.length) {
        setActive(tabs[0].name);
      }
    }

  }, [tabs, activeTab]);

  return {
    tabs,
    activeTab: active,
    initTab,
    setActiveTab: handleTabChange,
  }
}
const context = createContext<ReturnType<typeof useTabsContext>>({} as any);

export const useTabs = () => useContext(context);

export const TabsProvider = ({ children, ...rest }: TabsProps) => {
  return (
    <context.Provider value={useTabsContext({...rest})} >
      {children}
    </context.Provider>
  )
}