import { RouteProps } from 'react-router-dom';

export type IPageRoute = Pick<RouteProps, | 'exact' | 'path'> & {
  title: string;
  path: string;
  icon?: string;
  parent?: () => IPageRoute,
  redirect?: () => IPageRoute,
  component: () => Promise<{ default: React.ComponentType<any> }>
  children?: IPageRoute[];
};

export type IAppMenu = {
  name: string;
  title: string;
  path: string;
  icon?: string;
  parent?: string;
}

export type NavigationProps = {
  routes: IPageRoute[];
};

export type RoutingProps = {
  routes: IPageRoute[]
}

export enum MenuOpenStatus { none, open, closing, closed }

export type NavMenusProps = {
  menus: IPageRoute[];
}

export type NavMenuProps = {
  menu: IPageRoute;
}

export type MenuShownStatus = 'none' | 'showing' | 'shown';