import React, { createContext, useContext } from 'react';
import {BrowserRouter} from 'react-router-dom';
import { Provider } from 'react-redux';
import {Routing} from 'library/common/navigation';
import {useInternal} from './use-internal';
import {AppProps} from './types';
import { store } from '../store';

const context = createContext<ReturnType<typeof useInternal>>({} as any);

export const App = ({ routes, menus }: AppProps) => (
  <Provider store={store}>
    <BrowserRouter>
      <context.Provider value={useInternal({ routes, menus })}>
        <Routing routes={routes} />
      </context.Provider>
    </BrowserRouter>
  </Provider>
)

export const useApp = () => useContext(context);