/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import styled from 'styled-components';
import { Checkbox } from 'library/components';
import { BaseNode, LabelProps } from '../hierarchy/types';
import { CheckableLabelProps } from './types';

export function getCheckableLabel<T extends BaseNode>({ onLabel, getIntent, toggleSelection, isChecked, isIntermediary }: CheckableLabelProps<T>) {

  function CheckableLabel<K extends BaseNode>({ row }: LabelProps<K>) {

    const renderLabel = (row: K) => {
      const Label = onLabel({ row } as any);
      if (typeof Label === "function") {
        return <Label />
      }
      return Label
    };
    const Label = renderLabel(row);
    const itent = getIntent(row as any);

    return (
      <LabelContainer className={itent}>
        <Checkbox value={isChecked(row as any)} intermediary={isIntermediary?.(row as any)} onChange={toggleSelection(row as any)} />
        {Label}
      </LabelContainer>
    )
  }

  return CheckableLabel;
}

const LabelContainer = styled.div`
  display: inline-flex;
  align-items: center;
  .fa {
    color: darkgray;
  }
`;
