const replaceRouteParam = (path: string, key: string, value: any) => path.replace(new RegExp(`:${key}`, 'g'), value);

export const getRouteUrl = (path: string, routeParams: object = {}) => {
  const url = Object.keys(routeParams)
    .reduce((path, param) => replaceRouteParam(path, param, (routeParams as any)[param]), path);

  return url;
};