import React from 'react';
import classnames from 'classnames';
import {Link, useRouteMatch} from 'react-router-dom';
import {NavMenuProps, NavMenusProps} from './types';

export const NavMenus = ({menus}: NavMenusProps) => {
  return (
    <>
      {menus.map(m => (
        <NavMenu key={m.title} menu={m} />
      ))}
    </>
  )
}

const NavMenu = ({ menu }: NavMenuProps) => {
  const {children} = menu;
  const match = useRouteMatch({ path: menu.path });
  const opened = !!match && children?.length;

  return (
    <li className={classnames(match && 'active', opened && 'nav--opened')}>
      <Link to={menu.path!} data-match={!!match} >
        {menu.icon && <i className={menu.icon} />} <span className='nav-label'>{menu.title}</span> 
        {children?.length && <span className="fa arrow" />}
      </Link>
      {children && (
        <ul className='nav nav-second-level collapse'>
          {children?.map(menu => (
            <NavMenu key={menu.path} menu={menu} />
          ))}
        </ul>
      )}
    </li>
  )
}