import React from 'react';
import { FocusStatus, UsePaginationProps} from './types';

export const usePagination = ({
  pageIndex, pageSize, totalCount, onPageIndexChange,
}: UsePaginationProps) => {
  const [stringValue, setStringValue] = React.useState<string>();
  const [focused, setFocused] = React.useState<FocusStatus>('idle');

  const totalPageCount = Math.ceil(totalCount / pageSize);

  const valueIsNumber = (value: string) => {
    const reg = /^([1-9]\d*|0)$/;
    return reg.test(value);
  };
  const valueIsEmpty = (value: string) => !value || (value.trim && !value.trim());

  const calcShowing = () => {
    let showing: any = pageSize * (pageIndex + 1);
    if (showing > totalCount) {
      showing = totalCount;
    }
    return showing;
  };

  const calcDisplayIndex = () => {
    if (focused === 'focus-in') {
      return Number(stringValue);
    }

    if (!totalCount) {
      return 0;
    }
    return !pageIndex ? 1 : pageIndex + 1;
  };

  const numericToString = () => (!pageIndex ? "1" : `${pageIndex + 1}`);

  const setPageIndexChange = () => {
    if (valueIsNumber(stringValue!)) {
      const number = Number(stringValue);
      if (pageIndex !== number - 1) {
        onPageIndexChange(number - 1);
      }
    }
  };

  const handleFocus = () => setFocused('focus-in');

  const handleBlur = (e: any, returnKeyPressed?: boolean) => {
    if (!returnKeyPressed) {
      setFocused('focus-out');
    }
    if (valueIsEmpty(stringValue!)) {
      setStringValue(numericToString());
    }
    if (returnKeyPressed) {
      setPageIndexChange();
    }
  };
  const handleFocusOut = () => {
    if (focused === 'focus-out') {
      setPageIndexChange();
    }
  };

  const handleChange = (e: any) => {
    const { value } = e.target;
    const isEmpty = valueIsEmpty(value);
    const isNumber = valueIsNumber(value);
    if (isEmpty || isNumber) {
      if (!isEmpty) {
        const number = Number(value);
        if (number > 0 && number <= totalPageCount) {
          setStringValue(value);
        }
      } else {
        setStringValue(value);
      }
    }
  };

  const handlePageIndexChange = () => setStringValue(numericToString());

  React.useEffect(handleFocusOut, [focused]);
  React.useEffect(handlePageIndexChange, [pageIndex]);

  return {
    prev: pageIndex > 0,
    next: pageIndex < totalPageCount - 1,
    totalCount,
    showing: calcShowing(),
    displayIndex: calcDisplayIndex(),
    onChange: handleChange,
    onFocus: handleFocus,
    onBlur: handleBlur,
    totalPageCount,
  };
};
