import React from 'react';
import { Table } from 'library/components/table';
import { BaseNode, ColumnHandlerProps, RowHandleProps } from './types';
import { ColumnHandle } from './components';

export function DefaultColumnHandle<T extends BaseNode>({...rest }: ColumnHandlerProps<T>) {
  return (
    <Table.Column>
      <ColumnHandle {...rest}/>
    </Table.Column>
  )
}

export function DefaultRow<T extends BaseNode>({row, ...rest }: RowHandleProps<T>) {
  return (
    <Table.Row>
      <DefaultColumnHandle<T> node={row} {...rest} />
    </Table.Row>
  )
}