import React, { useState, useEffect } from 'react';
import classnames from 'classnames';
import styled from 'styled-components';
import {useAppStoreSelector} from 'library/common/store';

type PageSpinnerProps = {
  fetching?: boolean;
  children: React.ReactNode;
}
export const PageSpinner = ({ fetching: buzy, children }: PageSpinnerProps) => {
  const _fetching = useAppStoreSelector(state => state.fetchdata.fetching);
  const [fetching, setFetching] = useState(_fetching || buzy);

  useEffect(() => {
    setFetching(_fetching || buzy);
  }, [buzy, _fetching])

  return (
    <Root className={classnames({ "sk-loading": fetching })}>
      <div className="sk-spinner sk-spinner-double-bounce">
        <div className="sk-double-bounce1"></div>
        <div className="sk-double-bounce2"></div>
      </div>
      {children}
    </Root>
  )
}

const Root = styled.div`
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
`;