/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import { useFormikContext } from 'formik';
import DocumentTitle from 'react-document-title';
import { BaseItem } from './types';
import { usePage } from '../page/page-context';
import { useDataPage } from './data-page-provider';

type DataPageTitleProps<T extends BaseItem> = {
  onTitle: (item: T) => string;
}
export function DataPageTitle<T extends BaseItem>({ onTitle }: DataPageTitleProps<T>) {
  const { values } = useFormikContext<T>() || {};
  const { item } = useDataPage<T>();
  const [title, setTitle] = useState<string>();
  const { setPageTitle } = usePage();

  useEffect(() => {
    if (values) {
      setTitle(onTitle(values));
    }
  }, [values]);

  useEffect(() => {
    if (item && !title) {
      setTitle(onTitle(item))
    }
  }, [item])

  useEffect(() => {
    if (title) {
      setPageTitle(title);
    }
  }, [title])

  return title ? <DocumentTitle title={title} /> : <></>
}