import { useAppStoreSelector, bindAction } from 'library/common/store';
import { fetchData as _fetchData } from './fetch-data';
import { setFetchingAction } from './actions';
import { FetchDataProps, FetchResult } from './types';
import { config } from 'config';

export const useFetchData = () => {
  const fetching = useAppStoreSelector(state => state.fetchdata.fetching);
  const setFetching = bindAction(setFetchingAction);

  const fetchData = async <T>({ ...rest }: FetchDataProps<T>): Promise<FetchResult<T>> => {
    setFetching({ fetching: true });
    return _fetchData<T>({...rest})
      .finally(() => {
        setFetching({ fetching: false });
      })
  }

  const fetchItem = async <T>(props: {
    model: string;
    id: string;
    where?: object;
    expand?: string[];
  }) => fetchData({
    url: `${config.apiSwell}/${props.model}/${props.id}`, method: 'POST', body: {
    where: props.where,
    expand: props.expand
  } });

  return {
    fetching,
    fetchData,
    fetchItem,
  }
}