import React, { Suspense } from 'react';
import {
  Route, Redirect, Switch,
} from 'react-router-dom';

import {RoutingProps} from './types';

export const Routing = ({ routes }: RoutingProps) => {
  return (
    <Switch>
      <Route path="/" exact>
        <Redirect to="/home" />
      </Route>
      {
        routes.map((route, i) => {
          const redirect = route.redirect ? route.redirect().path : null;
          return (
            <Route key={i} exact={route.exact || !!redirect} path={route.path}>
              {redirect ? <Redirect to={redirect} /> : (<PageLazyLoad page={React.lazy(route.component)} />)}
            </Route>
          );
        })
      }
    </Switch>
  )
}

type PageLoazyLoadProps = {
  page: React.FC;
};
const PageLazyLoad = ({ page: Page, ...rest }: PageLoazyLoadProps) => (
  <Suspense fallback={<span>loading...</span>}>
    <Page {...rest} />
  </Suspense>
);