import { useAppStoreSelector, bindAction } from 'library/common/store';
import { config } from 'config';
import { useFetchData } from 'library/common/fetch-data';
import { setCategoriesAction } from './actions';
import { Category } from './types';

export const useCategories = () => {
  const { fetchData } = useFetchData();
  const categories = useAppStoreSelector(state => state.categories.items)
  const setCategories = bindAction(setCategoriesAction);

  const getCategories = async () => {
    let _categories = categories;
    if (!_categories) {
      const { data: { results } } = await fetchData<{ results: Category[] }>({
        url: `${config.apiSwell}/categories`
      });
      _categories = results;
      setCategories({ items: _categories });
    }
    return _categories;
  }

  return {
    getCategories,
  }
}

