import React from 'react';
import styled from 'styled-components';
import classnames from 'classnames';
import { ModalProps } from './types';
import { useModal } from './use-modal';
import { ModalHeader } from './components/modal-header';
import { ModalBody } from './components/modal-body';
import { ModalFooter } from './components/modal-footer';

export const Modal = ({ children, className, ...rest }: ModalProps) => {
  const { ref } = useModal({ ...rest });
  return (
    <Root ref={el => ref.current = el as any} className={classnames("modal inmodal fade", className)} tabIndex={-1} role="dialog" aria-hidden="true">
      <div className="modal-dialog modal-lg" >
        <div className="modal-content">
          {children}
          {/* <div className="modal-footer">
            <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
            <button type="button" className="btn btn-primary">Save changes</button>
          </div> */}
        </div>

      </div>

    </Root>
  )
};

Modal.Header = ModalHeader;
Modal.Body = ModalBody;
Modal.Footer = ModalFooter;

const Root = styled.div`
  .modal-body {
    border-radius: 4px
  }
`;