import React, { useRef } from 'react';
import styled from 'styled-components';
import { v4 } from 'uuid';
import { SwitchBoxProps } from './types';

export const SwitchBox = ({ label, checked: value, onChange }: SwitchBoxProps) => {
  const idRef = useRef(v4());
  const handleChange = () => onChange(!value);
  return (
    <div className="custom-control custom-switch">
      <input type="checkbox" checked={value} onChange={handleChange} className="custom-control-input" id={idRef.current} />
      <Label className="custom-control-label" htmlFor={idRef.current}>{label}</Label>
    </div>
  )
}

const Label = styled.label`
  line-height: 1.8;
`;