import React from 'react';
import {TabsProvider, useTabs} from './context';
import {TabsProps} from './types';
import {TabItem} from './tabitem';
import {Tab} from './tab';

export const Tabs = ({children, ...rest }: TabsProps) => (
  <TabsProvider {...rest}>
    <div className="tabs-container">
      <TabHeaders />
      <TabContent />
    </div>
    {children}
  </TabsProvider>
);

const TabHeaders = () => {
  const { tabs } = useTabs();
  return (
    <ul className="nav nav-tabs" role="tablist">
      {tabs.map(tab => (<TabItem key={tab.name} {...tab}/>))}
    </ul>
  )
}

const TabContent = () => {
  const { tabs, activeTab } = useTabs();
  const content = tabs.find(t => t.name === activeTab)?.children;
  return (
    <div className="tab-content">
      {content && (
        <div role="tabpanel" className="tab-pane active">
          <div className="panel-body">
            {content}
          </div>
        </div>
      )}
    </div>
  )
}

Tabs.Tab = Tab;