import React, { useContext, useEffect } from 'react';
import {createContext, useState} from 'react';

const useInternalContext = () => {
  const [pageTitle, setPageTitle] = useState<string>();

  useEffect(() => {
    document.body.classList.remove('fixed-sidebar');
    document.body.classList.add('fixed-sidebar');
  }, [])

  return {
    pageTitle,
    setPageTitle,
  }
}

const context = createContext<ReturnType<typeof useInternalContext>>({} as any);

export const usePage = () => useContext(context);

type PageContextProps = {
  children: React.ReactChild | React.ReactNode[];
}
export const PageContext = ({ children }: PageContextProps) => (
  <context.Provider value={useInternalContext()}>
    {children}
  </context.Provider>
)
