import { createStore, applyMiddleware, compose } from 'redux';
import { useSelector, TypedUseSelectorHook } from 'react-redux';

import { reducers } from './reducers';
import { AppStoreState } from './types';

const composeEnhancers = (window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

export const store = createStore(reducers, composeEnhancers(applyMiddleware()));
export const useAppStoreSelector: TypedUseSelectorHook<AppStoreState> = useSelector;

type InferableFunction = (...args: any) => any;
type ResolveArgs<T extends InferableFunction> = Parameters<T>[0];

export const bindAction = <T extends InferableFunction>(fn: T) => (
  payload: ResolveArgs<T>,
) => store.dispatch(fn(payload)) as void;