import React from 'react';
import {Link} from 'react-router-dom';
import classnames from 'classnames';
import styled, {css} from 'styled-components';
import {getRouteUrl} from 'library/common/navigation/utils';

type TableColumnProps = {
  className?: string;
  width?: number | string;
  children: React.ReactNode;
}
export const Column = ({ className, width, children }: TableColumnProps) => {
  return (
    <Td width={width} className={classnames('p-sm', className)}>
      {children}
    </Td>
  )
}

type TextColumnProps = Omit<TableColumnProps, 'children'> & {
  children: string;
}
export const TextColumn = ({ children, ...rest }: TextColumnProps) => {
  return (
    <Column {...rest}>
      <TextSpan>{children}</TextSpan>
    </Column>
  )
};

type LinkColumnProps = TextColumnProps & {
  path: string;
  params?: object
};
export const LinkColumn = ({ path, params, children: text, ...rest }: LinkColumnProps) => {
  return (
    <Column {...rest}>
      <LinkText to={getRouteUrl(path, params)} >{text}</LinkText>
    </Column>
  )
}

export const ImageColumn = ({ src, ...rest }: Omit<TableColumnProps, 'children'> & { src?: string }) => (
  <ImageColumnContainer {...rest}>
    {src && <img src={src} alt="column-img" />}
  </ImageColumnContainer>
)

export const CurrencyColumn = ({ value, fallback, ...rest }: Omit<TableColumnProps, 'children'> & { value?: any, fallback?: any }) => (
  <NumericColumnContainer {...rest}>
    {value ? Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(value) : fallback}
  </NumericColumnContainer>
)

const ImageColumnContainer = styled(Column)`
  width: 10% !important;
  img {
    width: 44px !important;
    height: 44px !important;
  }
`;

const Td = styled.td`
  vertical-align: middle !important;
  padding-top: 15px !important;
  padding-bottom: 15px !important;
`;
const textSize = css`
  font-size: 1rem;
`;
const TextSpan = styled.span`
  ${textSize};
`;
const LinkText = styled(Link)`
  ${textSize};
`;

export const NumericHeader = styled.div`
  text-align: right;
`;

const NumericColumnContainer = styled(TextColumn)`
  text-align: right;
`;