/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from 'react';
import { useTabs } from './context';
import { TabProps } from './types';

export const Tab = (props: TabProps) => {
  const { initTab } = useTabs();

  useEffect(() => {
    initTab(props);
  }, [])

  return <></>
}