/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { useEffect, useState } from 'react';
import { maxBy } from 'lodash';
import UrlPattern from 'url-pattern';
import { useLocation } from 'react-router-dom';
import { useApp } from 'library/common/app';
import {usePage} from './page-context';
import { IPageRoute } from 'library/common/navigation/types';

export const useTopbar = () => {
  const getPageTitle = () => crumbRoutes.length ? crumbRoutes[crumbRoutes.length - 1].title : undefined;
  const { routes } = useApp();
  const [crumbRoutes, setCrumbRoutes] = useState<IPageRoute[]>([]);
  const {pathname} = useLocation();
  const [pageTitle, setPageTitle] = useState(getPageTitle());

  const {pageTitle: appPageTitle} = usePage();

  const buildCrumbRoutes = () => {
    let _route = maxBy(routes.filter(r => !!(new UrlPattern(r.path).match(pathname))), r => r.path);
    let _routes: IPageRoute[] = [];
    if (_route) {
      do {
        _routes = [_route, ..._routes,];
        _route = _route.parent ? _route.parent() : undefined;
      } while (!!_route);
      if (!_routes.find(r => r.path === '/home')) {
        _routes = [routes.find(r => r.path === '/home')!, ..._routes];
      }
    }
    setCrumbRoutes(_routes);
  }

  useEffect(buildCrumbRoutes, [pathname]);

  useEffect(() => {
    if (appPageTitle && (appPageTitle !== pageTitle)) {
      setPageTitle(appPageTitle);
    } else {
      setPageTitle(getPageTitle())
    }
  }, [appPageTitle, crumbRoutes])

  return {
    crumbRoutes,
    pageTitle,
  }
}