import React from 'react';
import styled from 'styled-components';
import classnames from 'classnames';
import { CheckboxProps } from './types';

export const Checkbox = ({ label, value, intermediary, onChange }: CheckboxProps) => {
  const handleChange = (e: any) => onChange(!value);
  return (
    <LabelCheckbox >
      <input type="checkbox" onChange={handleChange} checked={value} />
      <Icon className="mr-2">
        <i className={classnames('fa',
          { 'fa-check-square text-success': value },
          { 'fa-square-o': !value && !intermediary },
          { 'fa-minus-square text-info': !value && intermediary })}
        />
      </Icon>
      {label}
    </LabelCheckbox>
  )
}

const LabelCheckbox = styled.label`
  position: relative;
  display: inline-flex;
  cursor: point;
  user-select: none;
  margin-bottom: 0px;

  .fa {
    font-size: 20px;
    margin-top: 3px;
  }

  input {
    position: absolute;
    display: none;
    cursor: pointer;
    height: 0;
    width: 0;
  }
`;

const Icon = styled.div`
  display: inline-block;
  width: 16px;
  cursor: pointer;
`;