import React, {createContext, useContext} from 'react';
import {useInternalDataPage} from './use-data-page';
import { DataPageProviderProps, BaseItem, DataPageContext } from './types';

type TContext = ReturnType<typeof useInternalDataPage>;

export const context = createContext<TContext>({} as any);

export function useDataPage<T extends BaseItem>() {
  return (useContext(context) as any) as DataPageContext<T>;
}
const { Provider } = context;
export function DataPageProvider<T extends BaseItem>({ children, ...rest }: DataPageProviderProps<T>) {
  return (
    <Provider value={useInternalDataPage<T>({ ...rest }) as any}>
      {children}
    </Provider>
  )
}