/* eslint-disable react-hooks/exhaustive-deps */
import {useRef, useEffect} from 'react';
import {ModalProps} from './types';

export const useModal = ({
  shown, onClose, onCanClose, className, keyboardClose,
}: Omit<ModalProps, 'children'>) => {
  const ref = useRef();

  useEffect(() => {

    $(ref.current as any).on('hide.bs.modal', function (e) {
      if (onCanClose && !onCanClose()) {
        e.preventDefault();
        e.stopImmediatePropagation();
        return false;
      }
    });

    $(ref.current as any).on('hidden.bs.modal', function (e) {
      onClose();
    });

    if (shown) {
      $(ref.current as any).modal(keyboardClose ? { backdrop: 'static', keyboard: false } : {});
    }

  }, [shown]);

  useEffect(() => {
    return () => {
      onClose();
      $(ref.current as any).modal('hide');
    }
  }, []);

  return {
    ref,
  }

}